.root {
  .productCard {
    display: flex;
    border: 1px solid #E8E9EA;
    height: 400px;
    margin-bottom: 2.5rem;

    .images {
      width: 50%;
      border-right: 1px solid #E8E9EA;

      .swiperSlide {
        height: 398px;

        img {
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }

    }

    .info {
      padding: 1.5rem;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .title {
        font-size: 34px;
        font-weight: 600;
        color: #171B24;
        margin-bottom: 1.5rem;
      }

      .price {
        font-size: 24px;
        font-weight: 600;
        color: #171B24;
        margin-bottom: 1.5rem;
      }

      .actions {
        display: flex;

        .toCartBtn {
          width: 250px;
          margin-right: 1rem;
        }

        .cartSum {
          display: flex;
          align-items: center;
          border: 1px solid #D1D1D3;

          button {
            width: 48px;
            height: 48px;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 24px;

            &:first-child {
              border-right: 1px solid #D1D1D3;
            }

            &:last-child {
              border-left: 1px solid #D1D1D3;
            }

            &:disabled {
              opacity: .5;
              cursor: no-drop;
            }
          }

          span {
            width: 48px;
            font-size: 24px;
            text-align: center;
          }
        }

        
      }
    }
  }

  .descriptionCard {
    color: #171B24;
    border: 1px solid #E8E9EA;
    padding: 3.75rem 2rem;

    .content {
      max-width: 768px;
      margin: auto;

      .title {
        font-size: 34px;
        font-weight: 600;
        margin-bottom: 1.25rem;
      }
  
      p {
        font-size: 16px;
        font-weight: 400;
      }
    }

  }
}